.video {
    /* width: 80%;
    height: 500px;
    overflow: hidden;
    margin-left: 30px; */


    max-width: 100%;
    height: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    padding: 15px;
    box-sizing: border-box;
    gap: 10px;
}

.main-div-call {
    width: 100%;
}

